import Vue from 'vue'
import Router from 'vue-router'


import Error from './components/Error.vue'
import Login from './components/Login.vue'
import Settings from './components/Settings.vue'
import postsSetting from './components/Posts.vue'
import account from './components/Account.vue'
import Register from './components/Register.vue'
import categoriesSetting from './components/Categories.vue'
import Media from './components/Media.vue'
import { Store } from 'vuex';
Vue.use(Router)

const routes = [{
        path: '/',
        component: Login,
    },
    {
        path: '/settings',
        component: Settings,
        meta: {
            requiresAuth: true
        },
        children: [{
                path: 'posts',
                component: postsSetting,
            },
            {
                path: 'categories',
                component: categoriesSetting
            },
            {
                path: 'media',
                component: Media
            }
        ]
    }, {
        path: '/account',
        component: account,
        meta: {
            requiresAuth: true
        },
    },
    {
        path: '/*',
        component: Login,
    },
]

const router = new Router({
    routes: routes,
    mode: 'history',
});


router.beforeEach((to, from, next) => {
    let routerAutoCheck = window.Store.state.userIsAuthorized;
    var expiration = window.Store.state.expiration;
    if (expiration) {
        var now = new Date().getTime()
        var generete = Date.parse(expiration)
        var dif = ((generete - now) > 0) ? true : false
        if (!dif && routerAutoCheck) {
            routerAutoCheck = dif;
            window.Store.commit('setUserIsAuthenticated', false)

        }
    }
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (routerAutoCheck) {
            //USER IS Authenticated
            // 
            window.Store.commit('setUserIsAuthenticated', true)
            next()

        } else {
            router.push('/')
            window.Store.commit('setUserIsAuthenticated', false)

        }

    } else {
        next()
    }
})

export default router